import React from 'react'
import { Link } from 'gatsby'
import './in-page-navigation.scss'
import Scrollspy from 'react-scrollspy'

const InPageNavigation = ({ headings }) => {
  let itemsId = [];
  headings.map(heading => itemsId.push(heading.id))

  console.log('itemsId', itemsId)
  return (
    <React.Fragment>
      <div className='in-page-navigation__title'>PAGE CONTENTS</div>
      <Scrollspy className='in-page-navigation__list'
                 items={itemsId}
                 currentClassName='in-page-navigation__list--item-active'>
        {headings
        .filter(heading => heading.depth !== 1)
        .map(heading => (
          <li className='in-page-navigation__list--item'
              key={heading.value}>
            <Link className='in-page-navigation__list--item-item'
                  to={'#' + heading.id}>
              {heading.value}
            </Link>
          </li>
        ))}
      </Scrollspy>
    </React.Fragment>
  )
}

export default InPageNavigation
