import React, { useEffect } from 'react'
import { graphql } from 'gatsby'
import { Layout } from '../layout'
import { PostContentContainer } from '../components/post-content-container'
import * as ScrollManager from '../utils/scroll'
import 'katex/dist/katex.min.css'
import { BlogContent } from '../components/blog-content'
import { useRenderedCount } from '../hooks/useRenderedCount'
import { useIntersectionObserver } from '../hooks/useIntersectionObserver'
import { useScrollEvent } from '../hooks/useScrollEvent'
import * as EventManager from '../utils/event-manager'
import * as Dom from '../utils/dom'
import MoreInfo from '../components/more-info/more-info'
import SocialMedia from '../components/social-media/social-media'
import { CoverImage } from '../components/cover-image'
import './blog-post.scss'
import { Head } from '../components/head'
import PostContactForm from '../components/post-contact-form/post-contact-form'
import InPageNavigation from '../components/in-page-navigation/in-page-navigation'

const BASE_LINE = 80

function getDistance(currentPos) {
  return Dom.getDocumentHeight() - currentPos
}

export default ({ data, location }) => {
  //use watch to check for changes event

  useEffect(() => {
    ScrollManager.init()
    return () => ScrollManager.destroy()
  }, [])
  const post = data.markdownRemark
  const metaData = data.site.siteMetadata

  const { countOfInitialPost } = data.site.siteMetadata.configs
  const { title } = metaData
  const { title: postTitle, image } = post.frontmatter
  const [count, countRef, increaseCount] = useRenderedCount()
  const posts = data.allMarkdownRemark.edges.filter((post, index) => index < 3)
  useIntersectionObserver()
  useScrollEvent(() => {
    const currentPos = window.scrollY + window.innerHeight
    const isTriggerPos = () => getDistance(currentPos) < BASE_LINE
    const doesNeedMore = () =>
      posts.length > countRef.current * countOfInitialPost

    return EventManager.toFit(increaseCount, {
      dismissCondition: () => !isTriggerPos(),
      triggerCondition: () => isTriggerPos() && doesNeedMore(),
    })()
  })

  const isBrowser = () => typeof window !== 'undefined'
  const url = isBrowser() && window.location.href

  console.log('data: ', post)
  return (
    <Layout location={location} title={title}>
      <Head
        className="post-header-container__head"
        title={postTitle}
        description={post.excerpt}
      />

      <CoverImage imageFluid={image} title={postTitle} />

      <div className="blog-post">
        <div className="blog-post__content">
          <div className="blog-post__page-contents">
            <InPageNavigation headings={post.headings} />
          </div>
          <div className="blog-post__post">
            <PostContentContainer post={post} />
          </div>
          <div className="blog-post__social-media">
            <SocialMedia
              title={postTitle}
              url={url}
              hashtag={post.frontmatter.tags}
              summary={post.excerpt}
            />
          </div>
        </div>

        <div className="blog-post__more-info">
          <MoreInfo subject={postTitle} />
          <hr />
        </div>

        <div className="blog-post__suggestions">
          <h2 className="blog-post__suggestions--title">Ook interessant</h2>
          <BlogContent
            showAll={true}
            categoriesAsLink
            posts={posts}
            countOfInitialPost={countOfInitialPost}
            count={count}
            category={'All'}
            postsVisible={true}
          />
        </div>

        <div className="blog-post__social-media--mobile">
          <SocialMedia
            title={postTitle}
            url={url}
            hashtag={post.frontmatter.tags}
            summary={post.excerpt}
          />
        </div>

        <div className="blog-post__contact-form">
          <PostContactForm />
        </div>
      </div>
    </Layout>
  )
}

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        configs {
          countOfInitialPost
        }
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt(pruneLength: 280)
      html
      headings {
        depth
        value
        id
      }
      frontmatter {
        title
        category
        tags
        image {
          childImageSharp {
            fluid(maxWidth: 2000, quality: 100) {
              ...GatsbyImageSharpFluid
            }
          }
        }
        authors {
          id
          name
          bio
          profilepicture {
            childImageSharp {
              fluid {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
        date(formatString: "MMMM DD, YYYY")
      }
    }
    allMarkdownRemark(
      limit: 5
      filter: { frontmatter: { type: { eq: "blog" } } }
    ) {
      edges {
        node {
          id
          fields {
            authorIds
            slug
          }
          frontmatter {
            date
            title
            type
            tags
            image {
              childImageSharp {
                fluid {
                  ...GatsbyImageSharpFluid
                }
              }
            }
            authors {
              id
              name
            }
          }
        }
      }
    }
  }
`
