import React from 'react'
import './social-media.scss'
import { facebookIcon, linkedInIcon, twitterIcon } from './../../../content/assets/social-media'
import { FacebookShareButton, TwitterShareButton } from 'react-share'

const SocialMedia = ({ url, title, hashtag, summary }) => {
  return (
    <div className='social-media'>
      <FacebookShareButton url={url}
                           quote={summary}
                           hashtag={hashtag}>
        <div className='social-media__item clickable'>
          <div className='social-media__item--circle' />
          <img className='social-media__item--icon'
               src={facebookIcon}
               alt='facebook logo' />
        </div>
      </FacebookShareButton>

      <TwitterShareButton url={url}
                          title={title}
                          hashtag={hashtag}>
        <div className='social-media__item clickable'>
          <div className='social-media__item--circle' />
          <img className='social-media__item--icon'
               src={twitterIcon}
               alt='twitter logo' />
        </div>
      </TwitterShareButton>

      <a className='social-media__item clickable'
         href={`https://www.linkedin.com/shareArticle?mini=true&url=${url}&title=${title}&summary=${summary}&source='Ordina Bold Blog'`}
         target='_blank'
         rel='noopener'>
        <div className='social-media__item--circle' />
        <img className='social-media__item--icon'
             src={linkedInIcon}
             alt='linkedIn logo' />
      </a>

    </div>
  )
}

export default SocialMedia
