import React, { useEffect, useRef } from 'react'
import './index.scss'
import { PostHeaderContainer } from '../post-header-container/post-header-container'

export const PostContentContainer = ({ post }) => {
  const ref = useRef()

  const makeTableResponsive = (table) => {
    const headings = Array.from(
      table.querySelectorAll('thead > tr > th'),
    ).map(th => th.textContent)

    table.querySelectorAll('tbody > tr')
    .forEach((row) => {
      row.querySelectorAll('td').forEach((td, index) => {
        td.setAttribute('data-label', headings[index])
      })
    })
  }

  useEffect(() => {
    const current = ref.current
    if (!current) return

    current.querySelectorAll('table')
    .forEach(makeTableResponsive)
  }, [ref])

  const { title, date, authors } = post.frontmatter
  const { html, excerpt } = post

  return (
    <div className='post-content-container' ref={ref}>
      <div className='post-content-container__header'>
        <PostHeaderContainer
          title={title}
          date={date}
          authors={authors}
          description={excerpt}
        />
      </div>
      <div className='post-content-container__content'
           dangerouslySetInnerHTML={{ __html: html }} />
    </div>)
}
