import React from 'react'
import { Link } from 'gatsby'
import './index.scss'

export const PostPath = ({ links = [] }) => {
  return (
    <div className='breadcrumb'>
      {links.map((link, index) => (
        <span
          key={index}
          className={
            index === links.length - 1
              ? 'breadcrumb-item breadcrumb-item--last'
              : 'breadcrumb-item'
          }
        >
          <Link to={link.href}
                replace
                className={index === 0 ? 'breadcrumb-item-first' : ''}>{link.title}</Link>
          {links.length !== index + 1 ? <span className='arrow-icon' /> : null}
        </span>
      ))}
    </div>
  )
}
